import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			user_info: '',
			data:[]
		}
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.loadData();
		}else{
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}
	},
	methods: {
		loadData(){
			this.request({
				url: interfaces.userInfo,
				header: {
					token: this.user_info.token
				},
				method: 'POST',
				success: res => {
					if (res.code == 200) {
						this.data=res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		goFeedback(){
			wx.showToast({
				title: '功能未开放，敬请期待',
				icon: "none",
			});
			// wx.navigateTo({
			// 	url: '/pages/my/feedback',
			// })
		},
		goAddress(){
			wx.navigateTo({
				url: '/pages/address/index',
			})
		},
		tip() {
			wx.showToast({
				title: '功能未开放，敬请期待',
				icon: "none",
			});
		},
	}
}
